header {
    width: 100%;
    color: #0d0d0d;  
    padding: 15px;
    margin: 0;
    box-sizing: border-box;
  }
  
  .navigation {
    width: 100%;
  }
  
  .nav-holder {
    max-width: 960px;
    margin: 0 auto;
  }
  
  .nav {
    float: right;
  }
  
  .nav-list {
    list-style: none;
    padding: 0;
    margin: 0;
    text-align: right;
  }
  
  .nav-item {
    display: inline-block;
    margin-right: 15px;
  
    a {
      text-decoration: none;
      color: #090909;
      font-weight: bold;
  
      &:hover {
        text-decoration: underline;
      }
    }
  }
  
  body {
    background-image: url('img/bakkk.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }
  
  h1 {
    margin-right: 80px;
    text-align: left;
    list-style: none;
    text-decoration: underline;
  }
  
  h2 {
    text-shadow: 0 0 3px #FF0000, 0 0 2px #0000FF;
  }
  
  .rer {
    text-align: left;
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    text-shadow: 5px 5px 10px red;
    font-size: 15.5px;
  }
  
  .photo-block {
    display: flex;
    max-width: 800px;
    margin: auto;
  
    img {
      width: 30%;
      margin-right: 1%;
      border-radius: 10px;
    }
  }
  
  .text-block {
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
  
    p {
      font-size: 17px;
  
      &:nth-child(2) {
        text-align: center;
      }
  
      &:nth-child(3) {
        text-align: right;
      }
    }
  }
  
  .list-block {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .section-title {
    color: #333;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
  }
  
  .intro-paragraph {
    color: #666;
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .photo-processing-list {
    list-style-type: none;
    padding: 0;
  }
  
  .processing-item {
    font-size: 18px;
    margin-bottom: 8px;
  
    &::before {
      content: '\2665';
      margin-right: 8px;
      color: #e44d26;
    }
  }
  
  .table-block {
    text-align: center;
    width: 22%;
    margin-right: auto;
    float: left;
    background-color: #333;
    color: #fff;
    border-collapse: collapse;
  }
  
  th, td {
    padding: 30px;
    border: 1px solid #666;
  }
  
  th {
    text-align: center;
    background-color: #555;
  }
  
  tr {
    &:nth-child(even) {
      background-color: #444;
    }
  
    &:nth-child(odd) {
      background-color: #333;
    }
  }
  
  .photo-ch {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  

  
    img {
      border-radius: 20px;
  
   
  
      &:nth-child(1) {
        width: 180px;
        height: 200px;
        margin: 10px;
      }
  
      &:nth-child(2) {
        width: 180px;
        height: 250px;
        margin: 5px;
      }
  
      &:nth-child(3) {
        width: 200px;
        height: 300px;
        margin: 5px;
      }
  
      &:nth-child(4) {
        width: 280px;
        height: 350px;
        margin: 5px;
      }
    }
  }
  
  button {
    padding: 10px 20px;
    background-color: #eceff0;
    border: 1px solid #eaeef0;
    text-align: center;
    text-decoration: none;
    color: #0a0a0a;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    display: block;
    margin: 0 auto;
  }
  
  footer {
    width: 100%;
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 15px;
    margin: 0;
    box-sizing: border-box;
  }
  
  @media only screen and (min-width: 769px) {
    .nav {
      float: right;
    }
  
    .nav-list {
      text-align: right;
    }
  
    .nav-item {
      display: inline-block;
      margin-right: 15px;
  
      a {
        text-decoration: none;
        color: #090909;
        font-weight: bold;
  
        &:hover {
          text-decoration: underline;
        }
      }
    }
  
    .photo-block {
      display: flex;
      max-width: 800px;
      margin: auto;
  
      img {
        width: 30%;
        margin-right: 1%;
        border-radius: 10px;
      }
    }
  
    .table-block {
      width: 22%;
      margin-right: auto;
      float: left;
      background-color: #333;
      color: #fff;
      border-collapse: collapse;
    }
  }
  
  @media only screen and (max-width: 768px) {
    .nav {
      float: none;
    }
  
    .nav-list {
      text-align: center;
    }
  
    .nav-item {
      display: block;
      margin-right: 0;
      margin-bottom: 10px;
    }
  
    .photo-block {
      display: block;
      max-width: 100%;
      margin: auto;
  
      img {
        width: 100%;
        margin-right: 0;
        border-radius: 10px;
        margin-bottom: 10px;
      }
    }
  
    .table-block {
      width: 100%;
      float: none;
      margin-right: 0;
    }
  }

  
  @media only screen and (max-width: 480px) {
    .header, .nav-list {
      text-align: center;
    }
  
    .nav-item {
      display: block;
      margin-right: 0;
      margin-bottom: 10px;
    }
  
    .photo-block {
      display: block;
      max-width: 100%;
      margin: auto;
  
      img {
        width: 100%;
        margin-right: 0;
        border-radius: 10px;
        margin-bottom: 10px;
      }
    }
  }
  
  .menu-toggle {
    display: none;
  }
  
  @media only screen and (max-width: 768px) {
    .menu-toggle {
      display: block;
      margin-bottom: 10px;
    }
  
    .nav-list {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 50px;
      right: 0;
      background-color: #f9f9f9;
      padding: 10px;
      border: 1px solid #ddd;
    }
  
    .nav-open .nav-list {
      display: flex;
    }
  }
  